    /* Medium Devices, Desktops */
    @media (max-width : 992px) {

        nav {

            a{
                display: none;
            }
        }
        
         #logo-holder {
            
        width: 75%;    
        margin: 0 auto;
        padding: 0;
        position: relative;
        margin-top: 15%;
        left:13%;

        }
        
        #img {
            width: 100%;
            right: 12%;
        }
        
        #txt {
            margin: 0 auto;
            padding: 0;
            width: 100%;
            position: relative;
            right: 0%;
            left: 40%;
            padding-top: 5%;
        }
        
        
        
        #links {
            
            .media-links {
            
                width: 8%;
                
            }
        }
    }

    /* Large Devices, Wide Screens */
